export class MarkerMaker{
	constructor(extraConfig={},data=[]) {  // data:所有车的经纬度数据
	  this.defaultConfig = { // 默认配置
			hasJoinCluster:true // 指定了该参数才会参与聚合
		}
		
		this.config = { // merge 后的配置
			...this.defaultConfig,
			...extraConfig
		}
		
		this.data = data
		
		this.markerBase = {
			width: 50,
			height: 50,
			joinCluster: this.config.hasJoinCluster, // 指定了该参数才会参与聚合
			label: {
				// content: item.lable,
				width: 50,
				height: 30,
				color: '#bbb', //#26bfb5
				fontSize: 10,
				borderWidth: 1,
				borderRadius: 10,
				// borderColor: '#000000',
				bgColor: '#ffffff',
				padding: 1,
				textAlign: 'center',
			}
		}
		
		this.generateMarkers = this.generateMarkers.bind(this)
	}
	
	generateMarkers(){ // 生成markers列表
		const res = []
		this.data.forEach((item,index)=>{
			const newMarker = Object.assign(this.markerBase,{
				id:index+1,
				iconPath:item.isonline?'@/assets/img/carColor.png':'@/assets/img/carGrey.png',
				latitude: item.lat||'',
				longitude: item.lon||'',
			})
			// newMarker.label.content = `label ${i + 1}`
			res.push(newMarker)
		})
		return res
	}
}